body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', sans-serif;
  background: linear-gradient(to right, rgb(13, 255, 211), rgb(113, 103, 191));
}

 @font-face {
    font-family: 'Pixel Technology';
    font-style: normal;
    font-weight: normal;
    src: local('Pixel Technology'), url(/robofriends/static/media/pixtech.42e767fe.woff) format('woff');
    }

    h1 {
        font-family: 'Pixel Technology';
        font-weight: 200;
        background-image: linear-gradient(to right, rgb(56, 47, 141), rgb(214, 20, 99));
        -webkit-background-clip: text;
                background-clip: text;
        color: transparent;
    }
